import { Component } from "react";
import React from 'react';
import { useHistory } from "react-router-dom";
import {Button,Table} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
import Select from 'react-select';
class App extends Component {
  constructor(props) {
    super(props);


  this.state = {
    role:"ROLE_ADMIN",
    reasonTab:false,
    department:"",
    tickets:[],
    reasonsTablist:[],
    reasons:[],
    pageNo:0,
    lastPage:false,
    updateCalled :false,
    isLoading:false
  };
    this.getTickets = this.getTickets.bind(this);
    this.logout = this.logout.bind(this);
    this.reasonTab=this.reasonTab.bind(this);
    this.closereasonTab=this.closereasonTab.bind(this);
    this.getReasons = this.getReasons.bind(this);
    this.handleChangePageNo = this.handleChangePageNo.bind(this);
}
  async componentDidMount() {
    this.getUserDetails();
    this.getTickets(0);
    this.getReasons();
  }



  async getReasons(){
    let getReasons= await fetch('http://localhost:8080/reasons',
            {mode: 'no-cors',headers:({'Content-Type':"application/json",'Access-Control-Allow-Origin':'*'})
            ,method:'GET',credentials:'same-origin'});

            let response=await getReasons.json();
            if(getReasons.status===200){
            this.setState({'reasons':[]});
            console.log("reasons:",response);
            this.setState({"reasonsTablist":response});
                for(var i=0;i<response.length;i++){
                let re={ 'value': response[i].id, 'label': response[i].text };
                this.setState({ 'reasons': [...this.state.reasons, re] });
                }
            }
}
  async getUserDetails(){
  try{
    let getUserDetails= await fetch('http://localhost:8080/users/self',
            {mode: 'no-cors',headers:({'Content-Type':"application/json",'Access-Control-Allow-Origin':'*'})
            ,method:'GET',credentials:'same-origin'});
            let response= await getUserDetails.json();

                if(getUserDetails.status===200){
                if(response.authorities.length!==0){
                    localStorage.setItem("login",true);
                }
                this.setState({"role":response.authorities[0]});
                if(response.departments.length===1){
                    this.setState({"department":response.departments[0]});
                }else{
                    this.setState({"department":"ADMIN"});
                }
            }
            console.log("UserData: ", response);
  }catch(err){
  console.log("Error",err);
  this.props.changeCondition(false);
  }
  }
   async getTickets(pageNo){
   try{
   this.setState({isLoading: true});
    let getTickets = await fetch('http://localhost:8080/tickets?page='+pageNo+'&size=10&direction=DESC&properties=openTimestamp',
            {mode: 'no-cors',headers:({'Content-Type':"application/json",'Access-Control-Allow-Origin':'*'})
            ,method:'GET',credentials:'same-origin'});

            let response=await getTickets.json();
            if(getTickets.status==200){
            this.setState({'tickets':response.content, 'pageNo':response.pageable.pageNumber, 'lastPage':response.last});
            }
            console.log("tickets: ",response);}
  catch(err){
  console.log("Error ",err);
  }
  this.setState({updateCalled: false});
  this.setState({isLoading: false});
}

  async logout(){
    await fetch('http://localhost:8080/login?logout',{mode: 'no-cors',
          method: 'GET',
          headers: ({
         'Content-Type': 'application/json','Access-Control-Allow-Origin':'*'
          }),credentials:'same-origin'
        }).then(res=>{
            if(res.status===200){
                this.props.changeCondition(false);
                localStorage.removeItem("login");
            }
        }).catch(err=>{
            console.log(err);
        })
  }

  reasonTab(){
  this.setState({"reasonTab":true});
}

closereasonTab(){
  this.setState({"reasonTab":false});
  this.getTickets(0);
  this.getReasons();
}

handleChangePageNo(text){
    var pageNo=this.state.pageNo;
    if(text=="add"){
        pageNo=pageNo+1;
    }else{
        pageNo=pageNo-1;
    }
    this.getTickets(pageNo);
}


  render() {
    const tickets = this.state.tickets.filter(ticket=>ticket.reason===null).map(ticket =>
			<TicketTable  ticket={ticket} reasons={this.state.reasons}/>);
    return (
      <div>
      <div className="subheader">
        <label className="dep">Department - {this.state.department}</label>
            {this.state.role==="ROLE_ADMIN"?<div className="header-right">
              <a> <button onClick={this.closereasonTab}>Home</button></a>
              <a> <button onClick={this.reasonTab}>Reason Tab</button></a>
              <a><button onClick={this.logout}>Logout</button></a>
              </div>:
              <div className="header-right">
              <a><button onClick={this.logout}>Logout</button></a>
              </div>
            }
      </div>
      {this.state.reasonTab===true?
      <Reason
        reasons={this.state.reasonsTablist}
        getReasons={this.getReasons}
        />:<div>
          <Table  striped bordered hover size="sm">
            <thead className="tableheader">
              <tr>
                <th width="170" scope="col">Ticket ID</th>
                <th width="190" scope="col">Down Start Time</th>
                <th width="190" scope="col">Down Completed Time</th>
                <th width="870" scope="col">Reason</th>
                <th width="170" scope="col">Status</th>
              </tr>
            </thead>
            <tbody className="tablebody">
              {this.state.isLoading?<tr><td colspan={5}>Loading...</td></tr> :this.state.tickets.length!==0?tickets:<tr><td colspan={5}>No Records Found</td></tr>}
            </tbody>
          </Table>
          <div style={{"text-align": "center"}}>
            <a ><Button onClick={() => this.handleChangePageNo("")} disabled={this.state.pageNo==0 || this.state.isLoading?true:false}>Prev</Button></a>
            <a ><Button onClick={() => this.handleChangePageNo("add")} disabled={this.state.lastPage|| this.state.isLoading?true:false}>Next</Button></a>
            </div>
            </div>
  }
          </div>
    );
  }
}
export default App;




class TicketTable extends Component{
  constructor(props){
      super(props);
      this.state={
          id:0,
          openTimestamp:'',
          closedTimestamp:'',
          status:'',
          reason:'',
          reasonId:'',
          resolved:false
      }

      this.handleInputChange=this.handleInputChange.bind(this);

}
async componentDidMount(){
  this.setState({'id':this.props.ticket.id,'openTimestamp':this.props.ticket.openTimestamp,'closedTimestamp': this.props.ticket.closedTimestamp,'status':this.props.ticket.status})
}

async componentDidUpdate(prevState,prevProps){
    if(this.state.id!==this.props.ticket.id){
        this.setState({'id':this.props.ticket.id,'openTimestamp':this.props.ticket.openTimestamp,'status':this.props.ticket.status})
    }
}

async resolved(){
    this.setState({resolved :false});
  //this.setState({status:'Resolved'})
  console.log("Reason  ",this.state.reasonId);
  var url='http://localhost:8080/tickets?ticketId='+this.state.id+'&reasonId='+this.state.reasonId;
  let resolved=await axios.patch(url,
             {mode: 'no-cors',headers:({'Content-Type':"application/json",'Access-Control-Allow-Origin':'*'})
             ,credentials:'same-origin'});
                console.log("Resolved: ",resolved);
             let response=await resolved.data;
             if(resolved.status===200){
             this.setState({'id':response.id,'openTimestamp':response.openTimestamp,'closedTimestamp':response.closedTimestamp,'status':response.status,'reason':response.reason.text})
             if(response.status ==="CLOSED" && response.reason.id!==0){
                this.setState({resolved :true});
             }
             }
}

handleInputChange(value){
console.log("value: ",value);
this.setState({"reasonId":value.value});
}

render(){
  return(
      <tr>
      <td>{this.state.id}</td>
      <td>{this.state.openTimestamp}</td>
      <td>{this.state.closedTimestamp}</td>
      <td><Select options={this.props.reasons}

                onChange={this.handleInputChange}
                searchable={false}
                clearble={false}
                isDisabled={this.state.resolved?true:false}
                onClick={this.handleInputChange}
                placeholder="Select Reason"
                /></td>
      <td><Button disabled={this.state.resolved?true:false} onClick={() => this.resolved()}>{this.state.status}</Button></td>

  </tr>

  )

}
}


class Reason extends Component{
    constructor(props){
        super(props);
        this.state={
            reasons:[],
            department:"",
            id:0,
            departments:[{ value: 'MATERIAL', label: "MATERIAL" },{ value: 'MF', label: "MF" },{value: 'QUALITY', label: "QUALITY" },{value: 'SUPERVISOR', label: "SUPERVISOR" }],
            filterdepartments:[{ value: '', label: "All" },{ value: 'MATERIAL', label: "MATERIAL" },{ value: 'MF', label: "MF" },{value: 'QUALITY', label: "QUALITY" },{value: 'SUPERVISOR', label: "SUPERVISOR" }],
            reason_id:'',
            reason_text:"",
            selecteddepartment:"",
            isOpen:false
        }

        this.getReasons=this.getReasons.bind(this);
        this.openReason=this.openReason.bind(this);
        this.closeReason=this.closeReason.bind(this);
        this.saveReason=this.saveReason.bind(this);
        this.setReason_text=this.setReason_text.bind(this);
        this.setReason_id=this.setReason_id.bind(this);
    }

    async componentDidMount(){
    this.setState({"reasons":this.props.reasons})
    }
    componentDidUpdate(prevState, prevProps){
    if(prevState.reasons!==this.props.reasons){
        this.setState({"reason": this.props.reasons})
    }
    }
    getReasons(dept){
        console.log("get Reasons method called for department: ",dept);
        fetch(`http://localhost:8080/reasons/`+dept,
            {mode: 'no-cors',headers:({'Content-Type':"application/json",'Access-Control-Allow-Origin':'*'})
            ,method:'GET',credentials:'same-origin'}).then(res=>{
              let reasons=res.json();
              this.setState({"reasons":reasons});
               console.log("Reasons:: ",reasons);
           })
    }

    getDepartments(){
        console.log("Get all Departments method called");
        fetch(`http://localhost:8080/departments/`,
            {mode: 'no-cors',headers:({'Content-Type':"application/json",'Access-Control-Allow-Origin':'*'})
            ,method:'GET',credentials:'same-origin'}).then(res=>{
              let data=res.json();
              this.setState({"departments":data});
               console.log("Reasons:: ",data);
           })
    }

    handleInputChange=(value)=>{
        this.setState({'department':value.value})
        console.log(this.state.department);
       // this.getReasons(value.value);
    }

    async saveReason(){

        const requestOptions = {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body:JSON.stringify({"text":this.state.reason_text,"department":this.state.selecteddepartment})
            };
            const saveReason = await fetch('http://localhost:8080/reasons', requestOptions);

               console.log("SaveReason: ",saveReason);
            let response=await saveReason.data;
            if(saveReason.status===200){
                this.props.getReasons();
                this.setState({"isOpen":false});
            }
    }

    handleDepartmentChange=(value)=>{
    console.log("department ",value);
        this.setState({'selecteddepartment':value});
    }

    openReason(){
        this.setState({"isOpen":true})
    }
    closeReason(){
        this.setState({"isOpen":false})
    }
    setReason_id(e){
        this.setState({'reason_id':Number(e)});
    }
    setReason_text(e){
        this.setState({'reason_text':e});
    }
    render(){
        return(
        <div>
            <Button onClick={this.openReason}>Create Reason</Button>
            {this.state.isOpen===true?
            <div>
                <CreateReason
                    saveReason={this.saveReason}
                    departments={this.state.departments}
                    reason_text={this.setReason_text}
                    closeReason={this.closeReason}
                    handleDepartmentChange1={this.handleDepartmentChange}
                    reasonclass={this.state}
                />
                </div>:''}
                <div>
            <Select options={this.state.filterdepartments}
                onChange={this.handleInputChange}
                searchable={false}
                clearble={false}
                onClick={this.handleInputChange}
                placeholder="Select Department"
                /></div>
            <Table  striped bordered hover size="sm">
            <thead className="tableheader">
              <tr>
                <th scope="col">Reason Id</th>
                <th scope="col">Reason Text</th>
                <th scope="col">Department</th>
              </tr>
            </thead>
            <tbody className="tablebody">
              {this.state.department!==""?this.state.reasons.filter(reason => reason.department === this.state.department).map(reason => <tr>
                  <td>{reason.id}</td>
                  <td>{reason.text}</td>
                  <td>{reason.department}</td>
                </tr>):
                this.state.reasons.map(reason => <tr>
                                  <td>{reason.id}</td>
                                  <td>{reason.text}</td>
                                  <td>{reason.department}</td>
                                </tr>)
                }
            </tbody>
          </Table>
        </div>

    )}
}



class CreateReason extends Component {
  constructor() {
    super();
    this.myRef = React.createRef();
}
  handleDepartmentChange=(value)=>{
    this.props.handleDepartmentChange1(value.value);

  }
  render(){
    return (
      <div className="popup-box">
        <div className="box">
          <div className="border-box">
                        <h2>Create Reason</h2>
                        <label for="uname" id="un">Reason Text:</label><br/>
                        <input type="text" name="reason text" placeholder="Enter Reason" id="reason text" onChange={(e) => this.props.reason_text(e.target.value)} /><br />

                        <label id="department">Department:</label>
                        <Select ref={this.myRef}
                            options={this.props.departments}
                            onChange={this.handleDepartmentChange}
                            searchable={false}
                            clearble={false}
                            onClick={this.handleDepartmentChange}
                            placeholder="Select Department"
                        />
                        <br />
                        <Button  id="submit"  onClick={this.props.saveReason}>submit</Button>
                        <Button  id="Cancel"  onClick={this.props.closeReason}>Cancel</Button>
                    </div>
            </div>
        </div>
    );
  }
  };