import { Component, useState } from "react";
import App from "./App";
import Login from "./component/Login";

class Mainpage extends Component {
    constructor() {
      super();
      this.state={
          condition: false
      }

      this.changeCondition = this.changeCondition.bind(this);
      this.getUserDetails = this.getUserDetails.bind(this);
  }
  
 async componentDidMount(){
    if(localStorage.getItem("login")){
        this.getUserDetails();
    }
    }

  async getUserDetails(){
    try{
      let getUserDetails= await fetch('http://localhost:8080/users/self',
              {mode: 'no-cors',headers:({'Content-Type':"application/json",'Access-Control-Allow-Origin':'*'})
              ,method:'GET',credentials:'same-origin'});
              let response= await getUserDetails.json();
  
                  if(getUserDetails.status===200){
                      if(response.authorities.length!==0){
                            this.setState({condition : true});
                      }
                    }
              console.log("UserData: ", response);
    }catch(err){
        this.setState({condition : false});
        console.log("Error",err);
    }
    }

    changeCondition=(value)=>{
      this.setState({condition: value});
    }

    render(){
        return(
            <div>
                {
                    this.state.condition ? <App changeCondition={this.changeCondition}/>: <Login changeCondition={this.changeCondition}/>
                }
            </div>
    )
    };
}
export default Mainpage;