import React, { useState } from 'react';
import { Button, Form } from 'semantic-ui-react'
import { Redirect } from 'react-router-dom'
import App from '../App';
import { useHistory } from "react-router-dom";
export default function Login(props) {
    const history = useHistory();
    const [firstName, setFirstName] = useState('');
    const [password, setPassword] = useState('');

    const postData = () => {
        console.log(firstName);
        var formdata=new FormData();
        formdata.append("username",firstName);
        formdata.append("password",password);
        console.log(password);
        var details = {
            'username': firstName,
            'password': password
        };        
        var formBody = [];
        for (var property in details) {
          var encodedKey = encodeURIComponent(property);
          var encodedValue = encodeURIComponent(details[property]);
          formBody.push(encodedKey + "=" + encodedValue);
        }
        formBody = formBody.join("&");
         fetch('http://localhost:8080/login',{mode: 'no-cors',
          method: 'POST',
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
         // 'Content-Type': 'application/json'
          },credentials:'same-origin',
          body: formBody
        }).then(res=>{
            if(res.status===200){
                props.changeCondition(true);
            }
        }).catch(err=>{
           // props.changeCondition(false);
            console.log(err);
        })
          
    }
    return (
        <div>
    <div className='login'>
        <div className='login-header'>
            <h1>Open Tickets</h1>
        </div>
        <div className='login-form'>
        <Form >
            <Form.Field>
                <h3>UserName:</h3>
                <input type="text" placeholder='UserName' onChange={(e) => setFirstName(e.target.value)}/>
            </Form.Field>
            <Form.Field>
                <h3>Password:</h3>
                <input type="password" placeholder='Last Name' onChange={(e) => setPassword(e.target.value)}/>
            </Form.Field>
            <Button className='login-button' onClick={postData} type='submit'>Login</Button>
        </Form>
        </div>
    </div>
        </div>
    )
}